/* Styles pour l'Ã©chiquier */
.chessboard {
    display: grid;
    grid-template-columns: repeat(8, minmax(0, 1fr));
    grid-template-rows: repeat(8, minmax(0, 1fr));
    aspect-ratio: 1 / 1;
    width: 100%;
    max-width: 600px;
    margin: 0 auto;
    border: 2px solid #1f2937;
}

.chess-square {
    aspect-ratio: 1 / 1;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
}

.chess-square.light {
    background-color: rgb(254 243 199);
}

.chess-square.dark {
    background-color: rgb(146 64 14);
}

.chess-piece {
    font-size: 2rem;
    line-height: 1;
    position: relative;
    z-index: 10;
}

.ghost-piece {
    position: absolute;
    inset: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 2rem;
    line-height: 1;
    color: rgb(34 211 238);
    text-shadow: 0 0 8px cyan, 0 0 12px cyan;
    opacity: 0.8;
}